*{margin: 0;padding: 0;font-family: 'Noto Sans KR', sans-serif;}
li{list-style: none;}
a{text-decoration: none;color: black;cursor: pointer;}



/* ********************* header ***************************** */


.header{
    display: flex;
    width: 100%;
    height: 100px;
    line-height: 100px; 
    justify-content: space-between;   
}


.header .logo a{
    font-size: 0px;
    display: block;
    width: 140px;
    background-image: url(../img/logo_navy.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 58%;
}

.header .gnb ul{
    display: flex;
}

.header .gnb ul li a{
    padding: 0 20px;
    font-size: 15px;
    font-weight: 700;
}

.header .gnb ul li:last-child a{
    border-left: 1px solid black;
}


.header .icon ul{
    display: flex;
}

.header .icon{margin-right: 20px;}
.header .icon ul li{margin: 0 20px;}


/* ********************* page_Stage ***************************** */

.page_stage{
    width: 500px;
    margin: 70px auto 20px;
    position: relative;
}

.page_stage ul{
    display: flex;
    justify-content: space-between;
    text-align: center;
}


.page_stage ul::before{
    content: "";
    position: absolute;
    z-index: -1;
    width: calc(100% - 40px); 
    height: 1px;
    top: 30%;left: 12px;
    background-color: black;
    margin-left: 10px;
}

.page_stage ul .num{
    margin: 0 auto;
    text-align: center;
    background-color: white;
    height: 40px;width: 40px;
    border-radius: 50px;
    line-height: 40px;
    font-weight: bold;
    box-shadow: 1px 10px 30px rgba(128, 128, 128, 0.593);
}


/* 로그인 페이지 단계 표시 */
.page_stage ul li:nth-child(1) .num{
    background-color: #ccc;
    border: none;
}

.page_stage ul p{
    margin-top: 10px;

}


/* ********************* wrap ***************************** */

.wrap{
    width: 600px;
    margin: 0 auto 100px;
    padding: 20px;
}

.wrap h2{
    text-align: center;
    border-bottom: 1px solid #000;
    line-height: 3;
    margin-bottom: 30px;
}

.wrap .section{
    margin-bottom: 20px;
}

.wrap .section .agree_chk,
.wrap .section #agree_total{
    width: 15px;height: 15px;
}


.wrap .section span{
    font-weight: bold;
    color: red;
}

.wrap .section04 span{
    font-weight: normal;
    color: black;
}



.wrap .section p{
    margin-top: 10px;
    border: 1px solid #888;
    border-radius: 5px;
    padding: 20px;
    text-align: justify;
    font-size: 13px;
    height: 120px;
    overflow: auto;
}



.wrap .section04 div{
    color: #888;
    font-size: 13px;
    margin-top: 10px;
}


.wrap .btn{
    margin-top: 40px;
}


.wrap .btn li:nth-child(1) a button{
    float: left;margin-right: 2%;
    background-color: #fff;
}

.wrap .btn li:nth-child(2) a button{
    float: left;
    background-color: rgb(0,32,83);
    color: white;
}

.wrap .btn li a button{
    width: 49%;
    height: 50px;
    border: 1px solid rgb(0,32,83);
    margin-bottom: 10px;
    font-size: 15px;
    letter-spacing: 2px;
    border-radius: 30px;
    font-weight: bold;
    cursor: pointer;
}